.modal {
    display: none;
    position: fixed;
    top: 0; left: 0; right: 0; bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    justify-content: center;
    align-items: center;
  }
  
  .modal.is-open {
    display: flex;
  }
  
  .modal-content {
    background: white;
    padding: 20px;
    border-radius: 8px;
  }

