body {
  margin: 0;
  font-family: Arial, sans-serif;
}footer {
background-color: #f8f8f8;
display: flex;
justify-content: space-between;
align-items: center;
padding: 40px;
flex-wrap: wrap;
}
.container-footer{
  display: flex;
  justify-content: space-around;
}
.footer-logo {
display: flex;
align-items: center;
}

.footer-logo img {
height: 50px;
margin-right: 10px;
}
.footer-subscribe {
display: flex;
flex-direction: column;
align-items: flex-end;
}

.footer-subscribe input[type="email"] {
padding: 10px;
border-radius: 20px;
border: 1px solid #ccc;
margin-bottom: 10px;
width: 200px;
}

.footer-subscribe button {
padding: 10px 20px;
border: none;
border-radius: 20px;
background-color: black;
color: white;
cursor: pointer;
}

.modal {
display: none;
position: fixed;
z-index: 1000;
left: 0;
top: 0;
width: 100vw;
height: 100vh;
background-color: rgba(0, 0, 0, 0.5);
justify-content: center;
align-items: center;
}

.modal-content {
background-color: white;
padding: 40px;
border-radius: 20px;
position: relative;
text-align: center;
max-width: 400px;
width: 90%;
}

.modal-content .close {
position: absolute;
top: 10px;
right: 20px;
font-size: 24px;
cursor: pointer;
}

.modal-icons {
display: flex;
justify-content: space-between;
flex-wrap: wrap;
margin-top: 20px;
}

.modal-icons img {
width: 40px;
height: 40px;
margin: 10px;
}
